import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatch, RootState } from '../../store';
import {
  setup2FA,
  enable2FA,
  resetState,
  setSetupStep
} from '../../store/slices/twoFactorSlice';
import Button from '../ui/Button';
import LoadingSpinner from '../ui/LoadingSpinner';

export const TwoFactorSetup: React.FC = () => {
  const dispatch = useDispatch<AppDispatch>();
  const { qrCode, secret, backupCodes, loading, error, setupStep } = useSelector(
    (state: RootState) => state.twoFactor
  );
  const [token, setToken] = useState('');

  useEffect(() => {
    // Start 2FA setup when component mounts
    dispatch(setup2FA());

    // Cleanup on unmount
    return () => {
      dispatch(resetState());
    };
  }, [dispatch]);

  const handleVerification = async () => {
    if (token.length === 6) {
      await dispatch(enable2FA(token));
    }
  };

  const handleTokenChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value.replace(/\D/g, '').slice(0, 6);
    setToken(value);
  };

  if (loading) {
    return (
      <div className="flex justify-center items-center min-h-[200px]">
        <LoadingSpinner />
      </div>
    );
  }

  return (
    <div className="max-w-md mx-auto p-6 bg-white rounded-lg shadow-md">
      {error && (
        <div className="mb-4 p-3 bg-red-100 text-red-700 rounded">
          {error}
        </div>
      )}

      {setupStep === 'initial' && (
        <div className="text-center">
          <h2 className="text-2xl font-bold mb-4">Setting up Two-Factor Authentication</h2>
          <p className="text-gray-600 mb-4">
            Please wait while we generate your 2FA credentials...
          </p>
        </div>
      )}

      {setupStep === 'qr-shown' && qrCode && (
        <div>
          <h2 className="text-2xl font-bold mb-4">Scan QR Code</h2>
          <p className="text-gray-600 mb-4">
            Scan this QR code with your authenticator app (Google Authenticator, Authy, etc.)
          </p>
          
          <div className="mb-6 flex justify-center">
            <img src={qrCode} alt="2FA QR Code" className="border p-2 rounded" />
          </div>

          <div className="mb-6">
            <p className="text-sm text-gray-500 mb-2">
              If you can't scan the QR code, enter this code manually:
            </p>
            <code className="block p-2 bg-gray-100 rounded text-center select-all">
              {secret}
            </code>
          </div>

          <div className="mb-6">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Enter the 6-digit code from your authenticator app:
            </label>
            <input
              type="text"
              value={token}
              onChange={handleTokenChange}
              className="w-full px-3 py-2 border rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
              placeholder="000000"
            />
          </div>

          <Button
            onClick={handleVerification}
            disabled={token.length !== 6}
            className="w-full"
          >
            Verify and Enable 2FA
          </Button>
        </div>
      )}

      {setupStep === 'complete' && backupCodes && (
        <div>
          <h2 className="text-2xl font-bold mb-4">2FA Enabled Successfully!</h2>
          
          <div className="mb-6">
            <p className="text-gray-600 mb-4">
              Save these backup codes in a secure place. You can use them to access your account if you lose your authenticator device.
            </p>
            
            <div className="bg-gray-100 p-4 rounded mb-4">
              <div className="grid grid-cols-2 gap-2">
                {backupCodes.map((code, index) => (
                  <code key={index} className="block text-center select-all">
                    {code}
                  </code>
                ))}
              </div>
            </div>

            <p className="text-sm text-gray-500">
              * Each code can only be used once. Keep them safe!
            </p>
          </div>

          <Button
            onClick={() => dispatch(setSetupStep('initial'))}
            className="w-full"
          >
            Done
          </Button>
        </div>
      )}
    </div>
  );
}; 