import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import api from '../../lib/api';

interface TwoFactorState {
  isSetupMode: boolean;
  qrCode: string | null;
  secret: string | null;
  backupCodes: string[] | null;
  loading: boolean;
  error: string | null;
  setupStep: 'initial' | 'qr-shown' | 'verification' | 'complete';
}

const initialState: TwoFactorState = {
  isSetupMode: false,
  qrCode: null,
  secret: null,
  backupCodes: null,
  loading: false,
  error: null,
  setupStep: 'initial'
};

// Async thunks
export const setup2FA = createAsyncThunk(
  'twoFactor/setup',
  async (_, { rejectWithValue }) => {
    try {
      const response = await api.post('/api/auth/2fa/setup');
      return response.data;
    } catch (error: any) {
      return rejectWithValue(error.response?.data?.message || 'Failed to setup 2FA');
    }
  }
);

export const enable2FA = createAsyncThunk(
  'twoFactor/enable',
  async (token: string, { rejectWithValue }) => {
    try {
      const response = await api.post('/api/auth/2fa/enable', { token });
      return response.data;
    } catch (error: any) {
      return rejectWithValue(error.response?.data?.message || 'Failed to enable 2FA');
    }
  }
);

export const verify2FA = createAsyncThunk(
  'twoFactor/verify',
  async ({ email, token }: { email: string; token: string }, { rejectWithValue }) => {
    try {
      const response = await api.post('/api/auth/2fa/verify', { email, token });
      return response.data;
    } catch (error: any) {
      return rejectWithValue(error.response?.data?.message || 'Failed to verify 2FA');
    }
  }
);

export const disable2FA = createAsyncThunk(
  'twoFactor/disable',
  async (token: string, { rejectWithValue }) => {
    try {
      const response = await api.post('/api/auth/2fa/disable', { token });
      return response.data;
    } catch (error: any) {
      return rejectWithValue(error.response?.data?.message || 'Failed to disable 2FA');
    }
  }
);

export const verifyBackupCode = createAsyncThunk(
  'twoFactor/verifyBackup',
  async ({ email, code }: { email: string; code: string }, { rejectWithValue }) => {
    try {
      const response = await api.post('/api/auth/2fa/backup/verify', { email, code });
      return response.data;
    } catch (error: any) {
      return rejectWithValue(error.response?.data?.message || 'Failed to verify backup code');
    }
  }
);

export const generateNewBackupCodes = createAsyncThunk(
  'twoFactor/generateBackup',
  async (_, { rejectWithValue }) => {
    try {
      const response = await api.post('/api/auth/2fa/backup/generate');
      return response.data;
    } catch (error: any) {
      return rejectWithValue(error.response?.data?.message || 'Failed to generate new backup codes');
    }
  }
);

const twoFactorSlice = createSlice({
  name: 'twoFactor',
  initialState,
  reducers: {
    resetState: (state) => {
      return initialState;
    },
    setSetupStep: (state, action) => {
      state.setupStep = action.payload;
    }
  },
  extraReducers: (builder) => {
    // Setup 2FA
    builder.addCase(setup2FA.pending, (state) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(setup2FA.fulfilled, (state, action) => {
      state.loading = false;
      state.qrCode = action.payload.qrcode;
      state.secret = action.payload.secret;
      state.setupStep = 'qr-shown';
    });
    builder.addCase(setup2FA.rejected, (state, action) => {
      state.loading = false;
      state.error = action.payload as string;
    });

    // Enable 2FA
    builder.addCase(enable2FA.pending, (state) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(enable2FA.fulfilled, (state, action) => {
      state.loading = false;
      state.backupCodes = action.payload.backupCodes;
      state.setupStep = 'complete';
    });
    builder.addCase(enable2FA.rejected, (state, action) => {
      state.loading = false;
      state.error = action.payload as string;
    });

    // Verify 2FA
    builder.addCase(verify2FA.pending, (state) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(verify2FA.fulfilled, (state) => {
      state.loading = false;
    });
    builder.addCase(verify2FA.rejected, (state, action) => {
      state.loading = false;
      state.error = action.payload as string;
    });

    // Disable 2FA
    builder.addCase(disable2FA.pending, (state) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(disable2FA.fulfilled, (state) => {
      state.loading = false;
      return initialState;
    });
    builder.addCase(disable2FA.rejected, (state, action) => {
      state.loading = false;
      state.error = action.payload as string;
    });

    // Verify Backup Code
    builder.addCase(verifyBackupCode.pending, (state) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(verifyBackupCode.fulfilled, (state) => {
      state.loading = false;
    });
    builder.addCase(verifyBackupCode.rejected, (state, action) => {
      state.loading = false;
      state.error = action.payload as string;
    });

    // Generate New Backup Codes
    builder.addCase(generateNewBackupCodes.pending, (state) => {
      state.loading = true;
      state.error = null;
    });
    builder.addCase(generateNewBackupCodes.fulfilled, (state, action) => {
      state.loading = false;
      state.backupCodes = action.payload.backupCodes;
    });
    builder.addCase(generateNewBackupCodes.rejected, (state, action) => {
      state.loading = false;
      state.error = action.payload as string;
    });
  }
});

export const { resetState, setSetupStep } = twoFactorSlice.actions;
export default twoFactorSlice.reducer; 