import { useState } from 'react';
import Button from '../../../components/ui/Button';
import LoadingSpinner from '../../../components/ui/LoadingSpinner';
import { LineChart, Line, XAxis, YAxis, Tooltip, ResponsiveContainer, CartesianGrid, ReferenceDot } from 'recharts';
import api from '../../../lib/api';

interface TradeLog {
  time: number;
  price: number;
  side: 'buy' | 'sell';
  quantity: number;
  balance: number;
}

interface BacktestResult {
  trades: TradeLog[];
  pnl: number;
  winRate: number;
  finalBalance: number;
}

const defaultParams = {
  symbol: 'BTCUSDT',
  shortPeriod: 10,
  longPeriod: 20,
  quantity: 0.001,
  initialBalance: 1000,
  interval: '1h',
  limit: 200,
};

export default function BacktestingPage() {
  const [params, setParams] = useState(defaultParams);
  const [result, setResult] = useState<BacktestResult | null>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    setParams({ ...params, [e.target.name]: e.target.value });
  };

  const handleNumberChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setParams({ ...params, [e.target.name]: parseFloat(e.target.value) });
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);
    setResult(null);
    try {
      const res = await api.post('/api/bots/backtest', params);
      setResult(res.data);
    } catch (err: any) {
      setError(err.response?.data?.message || 'Backtest failed');
    } finally {
      setLoading(false);
    }
  };

  // Prepare equity curve for chart
  const equityCurve = result?.trades.reduce<{ time: number; balance: number }[]>((acc, trade) => {
    acc.push({ time: trade.time, balance: trade.balance });
    return acc;
  }, []) || [];

  return (
    <div className="bg-white rounded-xl shadow-subtle p-8 border border-border max-w-3xl mx-auto mt-8">
      <h2 className="text-2xl font-bold mb-4">Backtesting</h2>
      <p className="text-accent mb-6">Run and review strategy backtests here. Powered by real Binance historical data.</p>
      <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-4 mb-8">
        <div>
          <label className="block text-sm font-medium text-black mb-1">Symbol</label>
          <input name="symbol" value={params.symbol} onChange={handleChange} className="w-full px-3 py-2 border rounded-md" />
        </div>
        <div>
          <label className="block text-sm font-medium text-black mb-1">Short MA Period</label>
          <input name="shortPeriod" type="number" min={1} value={params.shortPeriod} onChange={handleNumberChange} className="w-full px-3 py-2 border rounded-md" />
        </div>
        <div>
          <label className="block text-sm font-medium text-black mb-1">Long MA Period</label>
          <input name="longPeriod" type="number" min={1} value={params.longPeriod} onChange={handleNumberChange} className="w-full px-3 py-2 border rounded-md" />
        </div>
        <div>
          <label className="block text-sm font-medium text-black mb-1">Quantity</label>
          <input name="quantity" type="number" min={0.00001} step={0.00001} value={params.quantity} onChange={handleNumberChange} className="w-full px-3 py-2 border rounded-md" />
        </div>
        <div>
          <label className="block text-sm font-medium text-black mb-1">Initial Balance ($)</label>
          <input name="initialBalance" type="number" min={1} value={params.initialBalance} onChange={handleNumberChange} className="w-full px-3 py-2 border rounded-md" />
        </div>
        <div>
          <label className="block text-sm font-medium text-black mb-1">Interval</label>
          <select name="interval" value={params.interval} onChange={handleChange} className="w-full px-3 py-2 border rounded-md">
            <option value="1m">1m</option>
            <option value="5m">5m</option>
            <option value="15m">15m</option>
            <option value="1h">1h</option>
            <option value="4h">4h</option>
            <option value="1d">1d</option>
          </select>
        </div>
        <div>
          <label className="block text-sm font-medium text-black mb-1">Data Points</label>
          <input name="limit" type="number" min={50} max={1000} value={params.limit} onChange={handleNumberChange} className="w-full px-3 py-2 border rounded-md" />
        </div>
        <div className="flex items-end">
          <Button type="submit" disabled={loading} className="w-full">{loading ? <LoadingSpinner /> : 'Run Backtest'}</Button>
        </div>
      </form>
      {error && <div className="text-red-600 mb-4">{error}</div>}
      {result && (
        <div className="mt-8">
          <h3 className="text-lg font-bold mb-2">Results</h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
            <div className="bg-gray-50 rounded-lg p-4 text-center">
              <div className="text-xs text-gray-500">Final Balance</div>
              <div className="text-xl font-bold text-black">${result.finalBalance.toFixed(2)}</div>
            </div>
            <div className="bg-gray-50 rounded-lg p-4 text-center">
              <div className="text-xs text-gray-500">Total PnL</div>
              <div className={`text-xl font-bold ${result.pnl >= 0 ? 'text-green-600' : 'text-red-600'}`}>{result.pnl >= 0 ? '+' : ''}${result.pnl.toFixed(2)}</div>
            </div>
            <div className="bg-gray-50 rounded-lg p-4 text-center">
              <div className="text-xs text-gray-500">Win Rate</div>
              <div className="text-xl font-bold text-black">{(result.winRate * 100).toFixed(1)}%</div>
            </div>
          </div>
          <div className="mb-8">
            <h4 className="text-md font-semibold mb-2">Equity Curve</h4>
            <ResponsiveContainer width="100%" height={300}>
              <LineChart data={equityCurve} margin={{ top: 10, right: 30, left: 0, bottom: 0 }}>
                <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="time" tickFormatter={(t: number) => new Date(t).toLocaleDateString()} />
                <YAxis domain={['auto', 'auto']} />
                <Tooltip labelFormatter={(t: number) => new Date(t).toLocaleString()} />
                <Line type="monotone" dataKey="balance" stroke="#2563eb" strokeWidth={2} dot={false} />
                {/* Mark trades */}
                {result.trades.map((trade, idx) => (
                  <ReferenceDot key={idx} x={trade.time} y={trade.balance} r={4} fill={trade.side === 'buy' ? '#22c55e' : '#ef4444'} stroke="#fff" />
                ))}
              </LineChart>
            </ResponsiveContainer>
          </div>
          <div>
            <h4 className="text-md font-semibold mb-2">Trade Log</h4>
            <div className="overflow-x-auto">
              <table className="min-w-full divide-y divide-border">
                <thead className="bg-secondary">
                  <tr>
                    <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">Time</th>
                    <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">Side</th>
                    <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">Price</th>
                    <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">Quantity</th>
                    <th className="px-4 py-2 text-left text-xs font-medium text-accent uppercase tracking-wider">Balance</th>
                  </tr>
                </thead>
                <tbody className="bg-white divide-y divide-gray-100">
                  {result.trades.map((trade, idx) => (
                    <tr key={idx} className="hover:bg-blue-50 transition">
                      <td className="px-4 py-2 text-xs text-gray-500">{new Date(trade.time).toLocaleString()}</td>
                      <td className={`px-4 py-2 font-semibold ${trade.side === 'buy' ? 'text-green-600' : 'text-red-600'}`}>{trade.side.toUpperCase()}</td>
                      <td className="px-4 py-2">${trade.price.toFixed(2)}</td>
                      <td className="px-4 py-2">{trade.quantity}</td>
                      <td className="px-4 py-2">${trade.balance.toFixed(2)}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>
      )}
    </div>
  );
} 