import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { AppDispatch, RootState } from '../../../store';
import { getApiKeys, addApiKey, deleteApiKey, getApiKeyUsage, clearErrors, clearUsageInfo, testApiKey, clearTestResult } from '../../../store/slices/apiKeySlice';
import { Link } from 'react-router-dom';

export default function ApiKeysPage() {
  const dispatch = useDispatch<AppDispatch>();
  const { keys, loading, error, deleteError, usageInfo, testResult, testing } = useSelector((state: RootState) => state.apiKeys);

  const [exchange, setExchange] = useState('');
  const [newApiKey, setNewApiKey] = useState('');
  const [apiSecret, setApiSecret] = useState('');
  const [selectedKeyId, setSelectedKeyId] = useState<string | null>(null);
  const [showTestForm, setShowTestForm] = useState(false);
  const [showSuccessAlert, setShowSuccessAlert] = useState(false);
  const [successMessage, setSuccessMessage] = useState('');

  useEffect(() => {
    dispatch(getApiKeys());
  }, [dispatch]);

  // Clear errors when component unmounts or when switching keys
  useEffect(() => {
    return () => {
      dispatch(clearErrors());
      dispatch(clearUsageInfo());
      dispatch(clearTestResult());
    };
  }, [dispatch]);

  // Show success alert when API key is added successfully
  useEffect(() => {
    if (!loading && !error && successMessage) {
      setShowSuccessAlert(true);
      const timer = setTimeout(() => {
        setShowSuccessAlert(false);
        setSuccessMessage('');
      }, 5000);
      return () => clearTimeout(timer);
    }
  }, [loading, error, successMessage]);

  const handleAddKey = (e: React.FormEvent) => {
    e.preventDefault();
    if (exchange && newApiKey && apiSecret) {
      dispatch(addApiKey({ exchange, apiKey: newApiKey, apiSecret }))
        .then((result) => {
          if (addApiKey.fulfilled.match(result)) {
            setSuccessMessage('API key added and validated successfully!');
            // Refetch keys after adding a new one
            dispatch(getApiKeys());
            // Reset form
            resetForm();
          }
        });
    }
  };

  const handleTestKey = (e: React.FormEvent) => {
    e.preventDefault();
    if (exchange && newApiKey && apiSecret) {
      dispatch(testApiKey({ exchange, apiKey: newApiKey, apiSecret }));
    }
  };

  const handleDeleteKey = async (keyId: string) => {
    // First check usage
    const usageResult = await dispatch(getApiKeyUsage(keyId));
    
    if (getApiKeyUsage.fulfilled.match(usageResult)) {
      const usage = usageResult.payload;
      
      if (!usage.canDelete) {
        // Show usage info modal or alert
        setSelectedKeyId(keyId);
        return;
      }
    }
    
    // If can delete, proceed with confirmation
    if (window.confirm('Are you sure you want to delete this API key?')) {
      dispatch(deleteApiKey(keyId))
        .then((result) => {
          if (deleteApiKey.fulfilled.match(result)) {
            setSuccessMessage('API key deleted successfully!');
          }
        });
    }
  };

  const closeUsageModal = () => {
    setSelectedKeyId(null);
    dispatch(clearUsageInfo());
  };

  const resetForm = () => {
    setExchange('');
    setNewApiKey('');
    setApiSecret('');
    setShowTestForm(false);
    dispatch(clearTestResult());
  };

  const dismissAlert = () => {
    setShowSuccessAlert(false);
    setSuccessMessage('');
  };

  return (
    <div className="space-y-8">
      <div>
        <h1 className="text-2xl font-bold text-black">API Key Management</h1>
        <p className="text-accent">Securely manage your exchange API keys here.</p>
      </div>

      {/* Success Alert */}
      {showSuccessAlert && (
        <div className="bg-green-50 border border-green-200 rounded-lg p-4 flex justify-between items-start">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <svg className="h-5 w-5 text-green-400" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
              </svg>
            </div>
            <div className="ml-3">
              <p className="text-sm font-medium text-green-800">{successMessage}</p>
            </div>
          </div>
          <button
            onClick={dismissAlert}
            className="ml-auto -mx-1.5 -my-1.5 bg-green-50 text-green-500 rounded-lg focus:ring-2 focus:ring-green-600 p-1.5 hover:bg-green-200 inline-flex h-8 w-8 items-center justify-center"
          >
            <span className="sr-only">Dismiss</span>
            <svg className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
            </svg>
          </button>
        </div>
      )}

      {/* Error Alert */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4 flex justify-between items-start">
          <div className="flex items-center">
            <div className="flex-shrink-0">
              <svg className="h-5 w-5 text-red-400" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
              </svg>
            </div>
            <div className="ml-3">
              <p className="text-sm font-medium text-red-800">{error}</p>
            </div>
          </div>
          <button
            onClick={() => dispatch(clearErrors())}
            className="ml-auto -mx-1.5 -my-1.5 bg-red-50 text-red-500 rounded-lg focus:ring-2 focus:ring-red-600 p-1.5 hover:bg-red-200 inline-flex h-8 w-8 items-center justify-center"
          >
            <span className="sr-only">Dismiss</span>
            <svg className="h-5 w-5" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M4.293 4.293a1 1 0 011.414 0L10 8.586l4.293-4.293a1 1 0 111.414 1.414L11.414 10l4.293 4.293a1 1 0 01-1.414 1.414L10 11.414l-4.293 4.293a1 1 0 01-1.414-1.414L8.586 10 4.293 5.707a1 1 0 010-1.414z" clipRule="evenodd" />
            </svg>
          </button>
        </div>
      )}

      {/* Add API Key Form */}
      <div className="bg-white p-6 rounded-lg shadow-subtle border border-border">
        <div className="flex justify-between items-center mb-4">
          <h2 className="text-lg font-semibold text-black">Add New API Key</h2>
          <button
            onClick={() => setShowTestForm(!showTestForm)}
            className="text-sm text-blue-600 hover:text-blue-800"
          >
            {showTestForm ? 'Hide Test' : 'Test API Key'}
          </button>
        </div>
        
        <form onSubmit={showTestForm ? handleTestKey : handleAddKey} className="space-y-4">
          <div>
            <label htmlFor="exchange" className="block text-sm font-medium text-black mb-1">Exchange</label>
            <select
              id="exchange"
              value={exchange}
              onChange={(e) => setExchange(e.target.value)}
              className="w-full px-3 py-2 bg-secondary rounded-md border border-border focus:outline-none focus:ring-2 focus:ring-black"
              required
            >
              <option value="">Select Exchange</option>
              <option value="binance">Binance</option>
              <option value="coinbase">Coinbase</option>
              <option value="kraken">Kraken</option>
            </select>
          </div>
          <div>
            <label htmlFor="apiKey" className="block text-sm font-medium text-black mb-1">API Key</label>
            <input
              id="apiKey"
              type="text"
              value={newApiKey}
              onChange={(e) => setNewApiKey(e.target.value)}
              placeholder="Your public API key"
              className="w-full px-3 py-2 bg-secondary rounded-md border border-border focus:outline-none focus:ring-2 focus:ring-black"
              required
            />
          </div>
          <div>
            <label htmlFor="apiSecret" className="block text-sm font-medium text-black mb-1">API Secret</label>
            <input
              id="apiSecret"
              type="password"
              value={apiSecret}
              onChange={(e) => setApiSecret(e.target.value)}
              placeholder="Your secret API key"
              className="w-full px-3 py-2 bg-secondary rounded-md border border-border focus:outline-none focus:ring-2 focus:ring-black"
              required
            />
          </div>
          
          {/* Test Results */}
          {testResult && (
            <div className="bg-green-50 border border-green-200 rounded-lg p-4">
              <div className="flex items-center mb-2">
                <svg className="h-5 w-5 text-green-400 mr-2" viewBox="0 0 20 20" fill="currentColor">
                  <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z" clipRule="evenodd" />
                </svg>
                <h3 className="text-green-800 font-semibold"> API Key Test Successful</h3>
              </div>
              <div className="text-green-700 text-sm space-y-1">
                <p><strong>Exchange:</strong> {testResult.exchange}</p>
                {testResult.accountInfo && (
                  <>
                    <p><strong>Trading Enabled:</strong> {testResult.accountInfo.canTrade ? 'Yes' : 'No'}</p>
                    <p><strong>Withdrawals:</strong> {testResult.accountInfo.canWithdraw ? 'Yes' : 'No'}</p>
                    <p><strong>Deposits:</strong> {testResult.accountInfo.canDeposit ? 'Yes' : 'No'}</p>
                  </>
                )}
                {testResult.balances && testResult.balances.length > 0 && (
                  <div>
                    <p><strong>Assets with balance:</strong> {testResult.totalAssets}</p>
                    <p><strong>Sample balances:</strong></p>
                    <ul className="ml-4">
                      {testResult.balances.slice(0, 3).map((balance: any, index: number) => (
                        <li key={index}>{balance.asset}: {balance.total.toFixed(8)}</li>
                      ))}
                    </ul>
                  </div>
                )}
                {testResult.note && (
                  <div className="bg-yellow-50 border border-yellow-200 rounded p-2 mt-2">
                    <p className="text-yellow-700 text-sm"><strong>Note:</strong> {testResult.note}</p>
                  </div>
                )}
              </div>
            </div>
          )}
          
          <div className="flex justify-between">
            <button
              type="button"
              onClick={resetForm}
              className="px-4 py-2 bg-gray-500 text-white font-semibold rounded-lg hover:bg-gray-600 transition-all"
            >
              Reset
            </button>
            <button
              type="submit"
              disabled={loading || testing}
              className="px-4 py-2 bg-black text-white font-semibold rounded-lg hover:bg-opacity-90 disabled:bg-opacity-50 transition-all"
            >
              {testing ? 'Testing...' : loading ? 'Adding...' : showTestForm ? 'Test Key' : 'Add Key'}
            </button>
          </div>
        </form>
      </div>

      {/* List of API Keys */}
      <div className="bg-white p-6 rounded-lg shadow-subtle border border-border">
        <h2 className="text-lg font-semibold text-black mb-4">Your API Keys</h2>
        {loading && keys.length === 0 ? (
          <p>Loading keys...</p>
        ) : keys.length === 0 ? (
          <p className="text-accent">You have not added any API keys yet.</p>
        ) : (
          <div className="overflow-x-auto">
            <table className="min-w-full divide-y divide-border">
              <thead className="bg-secondary">
                <tr>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-accent uppercase tracking-wider">Exchange</th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-accent uppercase tracking-wider">API Key</th>
                  <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-accent uppercase tracking-wider">Created At</th>
                  <th scope="col" className="relative px-6 py-3"><span className="sr-only">Actions</span></th>
                </tr>
              </thead>
              <tbody className="bg-white divide-y divide-border">
                {keys.map((key) => (
                  <tr key={key._id}>
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-black">{key.exchange}</td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-accent font-mono">{`${key.apiKey.substring(0, 8)}...`}</td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-accent">{new Date(key.createdAt).toLocaleDateString()}</td>
                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                      <button
                        onClick={() => handleDeleteKey(key._id)}
                        disabled={loading}
                        className="text-red-600 hover:text-red-900 disabled:text-gray-400"
                      >
                        Delete
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        )}
        {deleteError && (
          <div className="bg-red-50 border border-red-200 rounded-lg p-4 mt-4">
            <div className="flex items-center">
              <svg className="h-5 w-5 text-red-400 mr-2" viewBox="0 0 20 20" fill="currentColor">
                <path fillRule="evenodd" d="M10 18a8 8 0 100-16 8 8 0 000 16zM8.707 7.293a1 1 0 00-1.414 1.414L8.586 10l-1.293 1.293a1 1 0 101.414 1.414L10 11.414l1.293 1.293a1 1 0 001.414-1.414L11.414 10l1.293-1.293a1 1 0 00-1.414-1.414L10 8.586 8.707 7.293z" clipRule="evenodd" />
              </svg>
              <p className="text-red-800">{deleteError}</p>
            </div>
          </div>
        )}
      </div>

      {/* Usage Modal */}
      {selectedKeyId && usageInfo && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
          <div className="bg-white p-6 rounded-lg max-w-md w-full mx-4">
            <h3 className="text-lg font-semibold text-black mb-4">Cannot Delete API Key</h3>
            <p className="text-accent mb-4">
              This API key is currently being used by the following bots:
            </p>
            <div className="mb-4">
              {usageInfo.botsUsingKey.map((bot) => (
                <div key={bot.id} className="flex justify-between items-center py-2 border-b border-border last:border-b-0">
                  <span className="text-black font-medium">{bot.name}</span>
                  <span className={`px-2 py-1 rounded text-xs ${
                    bot.status === 'running' ? 'bg-green-100 text-green-800' :
                    bot.status === 'stopped' ? 'bg-gray-100 text-gray-800' :
                    'bg-red-100 text-red-800'
                  }`}>
                    {bot.status}
                  </span>
                </div>
              ))}
            </div>
            <p className="text-accent mb-4">
              Please delete or update these bots to use a different API key before deleting this one.
            </p>
            <div className="flex justify-between">
              <Link
                to="/dashboard/bots"
                className="px-4 py-2 bg-blue-600 text-white font-semibold rounded-lg hover:bg-blue-700 transition-all"
              >
                Manage Bots
              </Link>
              <button
                onClick={closeUsageModal}
                className="px-4 py-2 bg-black text-white font-semibold rounded-lg hover:bg-opacity-90 transition-all"
              >
                Close
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
} 