import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import api from '../../lib/api';

interface ApiKey {
  _id: string;
  exchange: string;
  apiKey: string; // This will be the decrypted key from the server
  createdAt: string;
}

interface ApiKeyUsage {
  apiKey: ApiKey;
  botsUsingKey: Array<{
    id: string;
    name: string;
    status: string;
  }>;
  canDelete: boolean;
}

interface ApiKeyState {
  keys: ApiKey[];
  loading: boolean;
  error: string | null;
  deleteError: string | null;
  usageInfo: ApiKeyUsage | null;
  testResult: any | null;
  testing: boolean;
  supportedExchanges: Array<{ value: string; label: string }>;
}

const initialState: ApiKeyState = {
  keys: [],
  loading: false,
  error: null,
  deleteError: null,
  usageInfo: null,
  testResult: null,
  testing: false,
  supportedExchanges: [
    { value: 'binance', label: 'Binance' },
    { value: 'coinbase', label: 'Coinbase Advanced Trade' },
    { value: 'kraken', label: 'Kraken' },
  ],
};

// Async thunk for getting API keys
export const getApiKeys = createAsyncThunk('apiKeys/getApiKeys', async (_, { rejectWithValue }) => {
  try {
    const response = await api.get('/api/keys');
    return response.data;
  } catch (error) {
    const err = error as { response?: { data?: { message?: string } } };
    return rejectWithValue(err.response?.data?.message || 'Failed to fetch API keys');
  }
});

// Async thunk for adding an API key
export const addApiKey = createAsyncThunk(
  'apiKeys/addApiKey',
  async ({ exchange, apiKey, apiSecret }: { exchange: string; apiKey: string; apiSecret: string }, { rejectWithValue }) => {
    try {
      const body = { exchange, apiKey, apiSecret };
      const response = await api.post('/api/keys', body);
      return response.data;
    } catch (error) {
      const err = error as { response?: { data?: { message?: string } } };
      return rejectWithValue(err.response?.data?.message || 'Failed to add API key');
    }
  }
);

// Async thunk for getting API key usage information
export const getApiKeyUsage = createAsyncThunk('apiKeys/getApiKeyUsage', async (keyId: string, { rejectWithValue }) => {
  try {
    const response = await api.get(`/api/keys/${keyId}/usage`);
    return response.data;
  } catch (error) {
    const err = error as { response?: { data?: { message?: string } } };
    return rejectWithValue(err.response?.data?.message || 'Failed to fetch API key usage');
  }
});

// Async thunk for deleting an API key
export const deleteApiKey = createAsyncThunk('apiKeys/deleteApiKey', async (keyId: string, { rejectWithValue }) => {
  try {
    await api.delete(`/api/keys/${keyId}`);
    return keyId;
  } catch (error) {
    const err = error as { response?: { data?: { message?: string } } };
    return rejectWithValue(err.response?.data?.message || 'Failed to delete API key');
  }
});

// Async thunk for testing an API key
export const testApiKey = createAsyncThunk(
  'apiKeys/testApiKey',
  async ({ exchange, apiKey, apiSecret }: { exchange: string; apiKey: string; apiSecret: string }, { rejectWithValue }) => {
    try {
      const body = { exchange, apiKey, apiSecret };
      const response = await api.post('/api/keys/test', body);
      return response.data;
    } catch (error) {
      const err = error as { response?: { data?: { message?: string } } };
      return rejectWithValue(err.response?.data?.message || 'Failed to test API key');
    }
  }
);

const apiKeySlice = createSlice({
  name: 'apiKeys',
  initialState,
  reducers: {
    clearErrors: (state) => {
      state.error = null;
      state.deleteError = null;
    },
    clearUsageInfo: (state) => {
      state.usageInfo = null;
    },
    clearTestResult: (state) => {
      state.testResult = null;
    },
  },
  extraReducers: (builder) => {
    builder
      // Get Keys
      .addCase(getApiKeys.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getApiKeys.fulfilled, (state, action: PayloadAction<ApiKey[]>) => {
        state.loading = false;
        state.keys = action.payload;
      })
      .addCase(getApiKeys.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      // Add Key
      .addCase(addApiKey.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(addApiKey.fulfilled, (state) => {
        state.loading = false;
        // We don't add the key to the state here.
        // We will refetch the list to ensure data is consistent.
      })
      .addCase(addApiKey.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      // Get Usage Info
      .addCase(getApiKeyUsage.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getApiKeyUsage.fulfilled, (state, action: PayloadAction<ApiKeyUsage>) => {
        state.loading = false;
        state.usageInfo = action.payload;
      })
      .addCase(getApiKeyUsage.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      // Delete Key
      .addCase(deleteApiKey.pending, (state) => {
        state.loading = true;
        state.deleteError = null;
      })
      .addCase(deleteApiKey.fulfilled, (state, action: PayloadAction<string>) => {
        state.loading = false;
        state.keys = state.keys.filter((key) => key._id !== action.payload);
        state.usageInfo = null; // Clear usage info after successful deletion
      })
      .addCase(deleteApiKey.rejected, (state, action) => {
        state.loading = false;
        state.deleteError = action.payload as string;
      })
      // Test API Key
      .addCase(testApiKey.pending, (state) => {
        state.testing = true;
        state.error = null;
        state.testResult = null;
      })
      .addCase(testApiKey.fulfilled, (state, action) => {
        state.testing = false;
        state.testResult = action.payload;
      })
      .addCase(testApiKey.rejected, (state, action) => {
        state.testing = false;
        state.error = action.payload as string;
        state.testResult = null;
      });
  },
});

export const { clearErrors, clearUsageInfo, clearTestResult } = apiKeySlice.actions;
export default apiKeySlice.reducer; 