import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { AppDispatch, RootState } from '../../../store';
import { getMe } from '../../../store/slices/authSlice';
import api from '../../../lib/api';
import { TwoFactorSetup } from '../../../components/auth/TwoFactorSetup';
import { disable2FA } from '../../../store/slices/twoFactorSlice';
import Button from '../../../components/ui/Button';

export default function ProfilePage() {
  const dispatch = useDispatch<AppDispatch>();
  const { user, loading } = useSelector((state: RootState) => state.auth);
  const [showSetup, setShowSetup] = useState(false);
  const [verificationToken, setVerificationToken] = useState('');

  const [name, setName] = useState(user?.name || '');
  const [email, setEmail] = useState(user?.email || '');
  const [currentPassword, setCurrentPassword] = useState('');
  const [newPassword, setNewPassword] = useState('');
  const [confirmPassword, setConfirmPassword] = useState('');
  const [error, setError] = useState('');
  const [success, setSuccess] = useState('');
  const [passwordError, setPasswordError] = useState('');

  useEffect(() => {
    if (!user) {
      dispatch(getMe());
    } else {
        setName(user.name || '');
        setEmail(user.email || '');
    }
  }, [user, dispatch]);

  const handleUpdateProfile = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    setError('');
    setSuccess('');
    try {
      await api.put(
        '/api/auth/profile',
        { name, email }
      );
      setSuccess('Profile updated successfully!');
    } catch (error) {
      const err = error as { response?: { data?: { message?: string } } };
      setError(err.response?.data?.message || 'Failed to update profile.');
    }
  };

  const handlePasswordChange = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (newPassword !== confirmPassword) {
      setPasswordError('New passwords do not match.');
      return;
    }
    setError('');
    setPasswordError('');
    try {
      const payload = {
        currentPassword,
        newPassword,
      };
      await api.put('/api/auth/change-password', payload);
      setSuccess('Password updated successfully!');
      // Clear password fields after successful update
      setCurrentPassword('');
      setNewPassword('');
      setConfirmPassword('');
    } catch (error) {
      const err = error as { response?: { data?: { message?: string } } };
      setPasswordError(err.response?.data?.message || 'Failed to update password.');
    }
  };

  const handleDisable2FA = async () => {
    if (!verificationToken) return;
    try {
      await dispatch(disable2FA(verificationToken)).unwrap();
      setVerificationToken('');
    } catch (error) {
      // Error is handled by the reducer
    }
  };

  if (loading) {
    return <div className="flex items-center justify-center h-full text-lg">Loading your profile...</div>;
  }

  return (
    <div className="max-w-4xl mx-auto py-6 sm:px-6 lg:px-8">
      <div className="bg-white shadow rounded-lg">
        <div className="px-4 py-5 sm:p-6">
          <h2 className="text-lg font-medium text-gray-900">Profile Settings</h2>
          
          {/* Profile Update Form */}
          <form onSubmit={handleUpdateProfile} className="mt-6">
            <div className="grid grid-cols-1 gap-y-6 gap-x-4 sm:grid-cols-6">
              <div className="sm:col-span-4">
                <label htmlFor="name" className="block text-sm font-medium text-gray-700">
                  Name
                </label>
                <div className="mt-1">
                  <input
                    type="text"
                    name="name"
                    id="name"
                    value={name}
                    onChange={(e) => setName(e.target.value)}
                    className="shadow-sm focus:ring-blue-500 focus:border-blue-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>

              <div className="sm:col-span-4">
                <label htmlFor="email" className="block text-sm font-medium text-gray-700">
                  Email
                </label>
                <div className="mt-1">
                  <input
                    type="email"
                    name="email"
                    id="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    className="shadow-sm focus:ring-blue-500 focus:border-blue-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>
            </div>

            {success && (
              <div className="mt-4 p-4 rounded-md bg-green-50">
                <p className="text-sm text-green-700">{success}</p>
              </div>
            )}

            {error && (
              <div className="mt-4 p-4 rounded-md bg-red-50">
                <p className="text-sm text-red-700">{error}</p>
              </div>
            )}

            <div className="mt-4">
              <Button type="submit">
                Update Profile
              </Button>
            </div>
          </form>

          {/* Password Change Form */}
          <div className="mt-10 pt-10 border-t border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">Change Password</h3>
            <form onSubmit={handlePasswordChange} className="mt-6">
              {user?.hasPassword && (
                <div className="sm:col-span-4">
                  <label htmlFor="currentPassword" className="block text-sm font-medium text-gray-700">
                    Current Password
                  </label>
                  <div className="mt-1">
                    <input
                      type="password"
                      name="currentPassword"
                      id="currentPassword"
                      value={currentPassword}
                      onChange={(e) => setCurrentPassword(e.target.value)}
                      className="shadow-sm focus:ring-blue-500 focus:border-blue-500 block w-full sm:text-sm border-gray-300 rounded-md"
                    />
                  </div>
                </div>
              )}

              <div className="mt-4 sm:col-span-4">
                <label htmlFor="newPassword" className="block text-sm font-medium text-gray-700">
                  New Password
                </label>
                <div className="mt-1">
                  <input
                    type="password"
                    name="newPassword"
                    id="newPassword"
                    value={newPassword}
                    onChange={(e) => setNewPassword(e.target.value)}
                    className="shadow-sm focus:ring-blue-500 focus:border-blue-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>

              <div className="mt-4 sm:col-span-4">
                <label htmlFor="confirmPassword" className="block text-sm font-medium text-gray-700">
                  Confirm New Password
                </label>
                <div className="mt-1">
                  <input
                    type="password"
                    name="confirmPassword"
                    id="confirmPassword"
                    value={confirmPassword}
                    onChange={(e) => setConfirmPassword(e.target.value)}
                    className="shadow-sm focus:ring-blue-500 focus:border-blue-500 block w-full sm:text-sm border-gray-300 rounded-md"
                  />
                </div>
              </div>

              {success && (
                <div className="mt-4 p-4 rounded-md bg-green-50">
                  <p className="text-sm text-green-700">{success}</p>
                </div>
              )}

              {passwordError && (
                <div className="mt-4 p-4 rounded-md bg-red-50">
                  <p className="text-sm text-red-700">{passwordError}</p>
                </div>
              )}

              <div className="mt-4">
                <Button type="submit">
                  Change Password
                </Button>
              </div>
            </form>
          </div>

          {/* 2FA Section */}
          <div className="mt-10 pt-10 border-t border-gray-200">
            <h3 className="text-lg font-medium text-gray-900">Two-Factor Authentication</h3>
            <div className="mt-3 max-w-xl text-sm text-gray-600">
              <p>
                Add an extra layer of security to your account by enabling two-factor
                authentication.
              </p>
            </div>

            {user?.twoFAEnabled ? (
              <div className="mt-5">
                <div className="rounded-md bg-green-50 p-4">
                  <div className="flex">
                    <div className="flex-shrink-0">
                      <svg
                        className="h-5 w-5 text-green-400"
                        xmlns="http://www.w3.org/2000/svg"
                        viewBox="0 0 20 20"
                        fill="currentColor"
                      >
                        <path
                          fillRule="evenodd"
                          d="M10 18a8 8 0 100-16 8 8 0 000 16zm3.707-9.293a1 1 0 00-1.414-1.414L9 10.586 7.707 9.293a1 1 0 00-1.414 1.414l2 2a1 1 0 001.414 0l4-4z"
                          clipRule="evenodd"
                        />
                      </svg>
                    </div>
                    <div className="ml-3">
                      <p className="text-sm font-medium text-green-800">
                        Two-factor authentication is enabled
                      </p>
                    </div>
                  </div>
                </div>

                <div className="mt-5">
                  <label
                    htmlFor="verification"
                    className="block text-sm font-medium text-gray-700"
                  >
                    Enter verification code to disable 2FA
                  </label>
                  <div className="mt-1 flex rounded-md shadow-sm">
                    <input
                      type="text"
                      name="verification"
                      id="verification"
                      value={verificationToken}
                      onChange={(e) => setVerificationToken(e.target.value.replace(/\D/g, '').slice(0, 6))}
                      className="flex-1 min-w-0 block w-full px-3 py-2 rounded-none rounded-l-md focus:ring-blue-500 focus:border-blue-500 sm:text-sm border-gray-300"
                      placeholder="Enter 6-digit code"
                    />
                    <Button
                      onClick={handleDisable2FA}
                      disabled={verificationToken.length !== 6}
                      className="-ml-px relative inline-flex items-center px-4 py-2 rounded-r-md"
                    >
                      Disable 2FA
                    </Button>
                  </div>
                </div>
              </div>
            ) : showSetup ? (
              <div className="mt-5">
                <TwoFactorSetup />
                <button
                  onClick={() => setShowSetup(false)}
                  className="mt-4 text-sm text-gray-600 hover:text-gray-900"
                >
                  Cancel Setup
                </button>
              </div>
            ) : (
              <div className="mt-5">
                <Button onClick={() => setShowSetup(true)}>
                  Enable Two-Factor Authentication
                </Button>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
} 