import { createSlice, createAsyncThunk, PayloadAction } from '@reduxjs/toolkit';
import api from '../../lib/api';

export interface MovingAverageStrategy {
  type: 'moving_average';
  parameters: {
    shortPeriod: number;
    longPeriod: number;
    symbol: string;
    quantity: number;
  };
}

export interface RSIStrategy {
  type: 'rsi';
  parameters: {
    period: number;
    overbought: number;
    oversold: number;
    symbol: string;
    quantity: number;
  };
}

export type BotStrategy = MovingAverageStrategy | RSIStrategy;

export interface Bot {
  _id: string;
  name: string;
  exchange: string;
  apiKeyRef: string;
  strategy: BotStrategy;
  status: 'stopped' | 'running' | 'error';
  performance?: {
    pnl: number;
    winRate: number;
    tradeCount: number;
    lastTradeAt?: string;
  };
  // Paper trading fields
  paperTrading: boolean;
  paperBalance: number;
  riskLimits: {
    maxDailyLoss: number;
    maxPositionSize: number;
    stopLoss: number;
    takeProfit: number;
  };
}

export interface BotLog {
  _id: string;
  bot: string;
  timestamp: string;
  type: string;
  message: string;
  data?: Record<string, string | number | boolean | null | undefined>;
}

export interface PaperTrade {
  _id: string;
  bot: string;
  user: string;
  symbol: string;
  side: 'buy' | 'sell';
  quantity: number;
  price: number;
  timestamp: string;
  status: 'filled' | 'pending' | 'cancelled' | 'failed';
  exchange: string;
  paperBalance: number;
  tradeType: 'paper' | 'real';
  pnl?: number;
}

export interface PaperTradingStats {
  currentBalance: number;
  totalPnL: number;
  winRate: number;
  totalTrades: number;
  totalReturn: number;
  riskLimits: {
    maxDailyLoss: number;
    maxPositionSize: number;
    stopLoss: number;
    takeProfit: number;
  };
  paperTrading: boolean;
}

interface BotState {
  bots: Bot[];
  selectedBot: Bot | null;
  logs: BotLog[];
  performance: Bot['performance'] | null;
  paperTrades: PaperTrade[];
  paperStats: PaperTradingStats | null;
  loading: boolean;
  error: string | null;
}

const initialState: BotState = {
  bots: [],
  selectedBot: null,
  logs: [],
  performance: null,
  paperTrades: [],
  paperStats: null,
  loading: false,
  error: null,
};

export const listBots = createAsyncThunk('bots/list', async (_, { rejectWithValue }) => {
  try {
    const res = await api.get('/api/bots');
    return res.data.bots;
  } catch (err) {
    const error = err as { response?: { data?: { message?: string } } };
    return rejectWithValue(error.response?.data?.message || 'Failed to fetch bots');
  }
});

export const createBot = createAsyncThunk('bots/create', async (data: Partial<Bot>, { rejectWithValue }) => {
  try {
    const res = await api.post('/api/bots', data);
    return res.data.bot;
  } catch (err) {
    const error = err as { response?: { data?: { message?: string } } };
    return rejectWithValue(error.response?.data?.message || 'Failed to create bot');
  }
});

export const updateBot = createAsyncThunk('bots/update', async ({ id, data }: { id: string; data: Partial<Bot> }, { rejectWithValue }) => {
  try {
    const res = await api.put(`/api/bots/${id}`, data);
    return res.data.bot;
  } catch (err) {
    const error = err as { response?: { data?: { message?: string } } };
    return rejectWithValue(error.response?.data?.message || 'Failed to update bot');
  }
});

export const deleteBot = createAsyncThunk('bots/delete', async (id: string, { rejectWithValue }) => {
  try {
    await api.delete(`/api/bots/${id}`);
    return id;
  } catch (err) {
    const error = err as { response?: { data?: { message?: string } } };
    return rejectWithValue(error.response?.data?.message || 'Failed to delete bot');
  }
});

export const toggleBot = createAsyncThunk('bots/toggle', async ({ id, action }: { id: string; action: 'start' | 'stop' }, { rejectWithValue }) => {
  try {
    const res = await api.post(`/api/bots/${id}/toggle`, { action });
    return res.data.bot;
  } catch (err) {
    const error = err as { response?: { data?: { message?: string } } };
    return rejectWithValue(error.response?.data?.message || 'Failed to toggle bot');
  }
});

