export const getPaperTradingStats = createAsyncThunk('bots/getPaperStats', async (id: string, { rejectWithValue }) => {
  try {
    const res = await api.get(`/api/bots/${id}/paper-stats`);
    return res.data.stats;
  } catch (err) {
    const error = err as { response?: { data?: { message?: string } } };
    return rejectWithValue(error.response?.data?.message || 'Failed to fetch paper trading stats');
  }
});

const botSlice = createSlice({
  name: 'bots',
  initialState,
  reducers: {
    setSelectedBot(state, action: PayloadAction<Bot | null>) {
      state.selectedBot = action.payload;
      state.logs = [];
      state.performance = null;
    },
    clearBotError(state) {
      state.error = null;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(listBots.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(listBots.fulfilled, (state, action) => {
        state.loading = false;
        state.bots = action.payload;
      })
      .addCase(listBots.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(createBot.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(createBot.fulfilled, (state, action) => {
        state.loading = false;
        state.bots.push(action.payload);
      })
      .addCase(createBot.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(updateBot.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(updateBot.fulfilled, (state, action) => {
        state.loading = false;
        state.bots = state.bots.map((bot) => (bot._id === action.payload._id ? action.payload : bot));
        if (state.selectedBot && state.selectedBot._id === action.payload._id) {
          state.selectedBot = action.payload;
        }
      })
      .addCase(updateBot.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(deleteBot.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(deleteBot.fulfilled, (state, action) => {
        state.loading = false;
        state.bots = state.bots.filter((bot) => bot._id !== action.payload);
        if (state.selectedBot && state.selectedBot._id === action.payload) {
          state.selectedBot = null;
          state.logs = [];
          state.performance = null;
        }
      })
      .addCase(deleteBot.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(toggleBot.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(toggleBot.fulfilled, (state, action) => {
        state.loading = false;
        state.bots = state.bots.map((bot) => (bot._id === action.payload._id ? action.payload : bot));
        if (state.selectedBot && state.selectedBot._id === action.payload._id) {
          state.selectedBot = action.payload;
        }
      })
      .addCase(toggleBot.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(getBotLogs.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getBotLogs.fulfilled, (state, action) => {
        state.loading = false;
        state.logs = action.payload;
      })
      .addCase(getBotLogs.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(getBotPerformance.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getBotPerformance.fulfilled, (state, action) => {
        state.loading = false;
        state.performance = action.payload;
      })
      .addCase(getBotPerformance.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      // Paper trading reducers
      .addCase(updatePaperTradingConfig.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(updatePaperTradingConfig.fulfilled, (state, action) => {
        state.loading = false;
        state.bots = state.bots.map((bot) => (bot._id === action.payload._id ? action.payload : bot));
        if (state.selectedBot && state.selectedBot._id === action.payload._id) {
          state.selectedBot = action.payload;
        }
      })
      .addCase(updatePaperTradingConfig.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(getPaperTrades.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getPaperTrades.fulfilled, (state, action) => {
        state.loading = false;
        state.paperTrades = action.payload;
      })
      .addCase(getPaperTrades.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      })
      .addCase(getPaperTradingStats.pending, (state) => {
        state.loading = true;
        state.error = null;
      })
      .addCase(getPaperTradingStats.fulfilled, (state, action) => {
        state.loading = false;
        state.paperStats = action.payload;
      })
      .addCase(getPaperTradingStats.rejected, (state, action) => {
        state.loading = false;
        state.error = action.payload as string;
      });
  },
});

export const { setSelectedBot, clearBotError } = botSlice.actions;
export default botSlice.reducer; 